import './App.css';
import React, {useState,useRef, useEffect} from "react";
import Sidebar from './Sidebar';
import Feed from './Feed';
import Right from './Right';
import {GoogleLogin} from 'react-google-login'
import db from './firebase'

function App() {

  const [name, setName] =useState('');
  const [email, setEmail] =useState('');
  const [avatar, setAvatar] =useState('');


  function responseGoogle (response){
    console.log(response);
    console.log(response.profileObj);

    setEmail(response.profileObj.email)           
    setName(response.profileObj.givenName)           
    setAvatar(response.profileObj.imageUrl)           
      
  }

  useEffect(() => {
    {
      db.collection('users').doc().set({
        username: name,
        email: email,
        avatar: avatar,
      })
    }
  }, [])
  
  return (
    <div className="abc">
        {!name? (
                          <GoogleLogin  
                          clientId="766075204483-lulb8u6h2g1v0h8kp0gsnirlntf170en.apps.googleusercontent.com"
                          buttonText="Login With Google"
                          onSuccess={responseGoogle}
                          onFailure={responseGoogle}
                          cookiePolicy={'single_host_origin'}
                 />
        ):( <div className='app'> 

         <Sidebar name={name} avatar={avatar}/>
         <Feed name={name} avatar={avatar}/>
         <Right/>
    </div>
     )}
    </div>
  );
}

export default App;